     "concepts":[
        {
           "title":"Patterns",
           "info":"Students observe patterns in systems at different scales and cite patterns as empirical evidence for causality in supporting their explanations of phenomena. They recognize classifications or explanations used at one scale may not be useful or need revision using a different scale; thus requiring improved investigations and experiments. They use mathematical representations to identify certain patterns and analyze patterns of performance in order to reengineer and improve a designed system."
        },
        {
           "title":"Cause and Effect",
           "info":"Students understand that empirical evidence is required to differentiate between cause and correlation and to make claims about specific causes and effects. They suggest cause and effect relationships to explain and predict behaviors in complex natural and designed systems. They also propose causal relationships by examining what is known about smaller scale mechanisms within the system. They recognize changes in systems may have various causes that may not have equal effects."
        },
        {
           "title":"Scale, Proportion, and Quantity",
           "info":"Students understand the significance of a phenomenon is dependent on the scale, proportion, and quantity at which it occurs. They recognize patterns observable at one scale may not be observable or exist at other scales, and some systems can only be studied indirectly as they are too small, too large, too fast, or too slow to observe directly. Students use orders of magnitude to understand how a model at one scale relates to a model at another scale. They use algebraic thinking to examine scientific data and predict the effect of a change in one variable on another (e.g., linear growth vs. exponential growth)."
        },
        {
           "title":"Systems and System Models",
           "info":"Students can investigate or analyze a system by defining its boundaries and initial conditions, as well as its inputs and outputs. They can use models (e.g., physical, mathematical, computer models) to simulate the flow of energy, matter, and interactions within and between systems at different scales. They can also use models and simulations to predict the behavior of a system, and recognize that these predictions have limited precision and reliability due to the assumptions and approximations inherent in the models. They can also design systems to do specific tasks."
        },
        {
           "title":"Energy and Matter: Flows, Cycles, and Conservation",
           "info":"Students learn that the total amount of energy and matter in closed systems is conserved. They can describe changes of energy and matter in a system in terms of energy and matter flows into, out of, and within that system. They also learn that energy cannot be created or destroyed. It only moves between one place and another place, between objects and/or fields, or between systems. Energy drives the cycling of matter within and between systems. In nuclear processes, atoms are not conserved, but the total number of protons plus neutrons is conserved."
        },
        {
           "title":"Structure and Function",
           "info":"Students investigate systems by examining the properties of different materials, the structures of different components, and their interconnections to reveal the systems function and/or solve a problem. They infer the functions and properties of natural and designed objects and systems from their overall structure, the way their components are shaped and used, and the molecular substructures of their various materials."
        },
        {
           "title":"Stability and Change",
           "info":"Students understand much of science deals with constructing explanations of how things change and how they remain stable. They quantify and model changes in systems over very short or very long periods of time. They see some changes are irreversible, and negative feedback can stabilize a system, while positive feedback can destabilize it. They recognize systems can be designed for greater or lesser stability."
        }
     ],
