     "concepts":[
        {
           "title":"Patterns",
           "info":"Students recognize that macroscopic patterns are related to the nature of microscopic and atomic-level structure. They identify patterns in rates of change and other numerical relationships that provide information about natural and human designed systems. They use patterns to identify cause and effect relationships, and use graphs and charts to identify patterns in data."
        },
        {
           "title":"Cause and Effect",
           "info":"Students classify relationships as causal or correlational, and recognize that correlation does not necessarily imply causation. They use cause and effect relationships to predict phenomena in natural or designed systems. They also understand that phenomena may have more than one cause, and some cause and effect relationships in systems can only be described using probability."
        },
        {
           "title":"Scale, Proportion, and Quantity",
           "info":"Students observe time, space, and energy phenomena at various scales using models to study systems that are too large or too small. They understand phenomena observed at one scale may not be observable at another scale, and the function of natural and designed systems may change with scale. They use proportional relationships (e.g., speed as the ratio of distance traveled to time taken) to gather information about the magnitude of properties and processes. They represent scientific relationships through the use of algebraic expressions and equations."
        },
        {
           "title":"Systems and System Models",
           "info":"Students can understand that systems may interact with other systems; they may have sub-systems and be a part of larger complex systems. They can use models to represent systems and their interactionssuch as inputs, processes and outputsand energy, matter, and information flows within systems. They can also learn that models are limited in that they only represent certain aspects of the system under study."
        },
        {
           "title":"Energy and Matter: Flows, Cycles, and Conservation",
           "info":"Students learn matter is conserved because atoms are conserved in physical and chemical processes. They also learn within a natural or designed system, the transfer of energy drives the motion and/or cycling of matter. Energy may take different forms (e.g. energy in fields, thermal energy, energy of motion). The transfer of energy can be tracked as energy flows through a designed or natural system."
        },
        {
           "title":"Structure and Function",
           "info":"Students model complex and microscopic structures and systems and visualize how their function depends on the shapes, composition, and relationships among its parts. They analyze many complex natural and designed structures and systems to determine how they function. They design structures to serve particular functions by taking into account properties of different materials, and how materials can be shaped and used."
        },
        {
           "title":"Stability and Change",
           "info":"Students explain stability and change in natural or designed systems by examining changes over time, and considering forces at different scales, including the atomic scale. Students learn changes in one part of a system might cause large changes in another part, systems in dynamic equilibrium are stable due to a balance of feedback mechanisms, and stability might be disturbed by either sudden events or gradual changes that accumulate over time."
        }
     ],
