     "Disciplinary Core Ideas (DCI)":"Motion energy is properly called kinetic energy; it is proportional to the mass of the moving object and grows with the square of its speed.; A system of objects may also contain stored (potential) energy, depending on their relative positions.; The temperature of a system is proportional to the average internal kinetic energy and potential energy per atom or molecule (whichever is the appropriate building block for the systems material). The details of that relationship depend on the type of atom or molecule and the interactions among the atoms in the material. Temperature is not a direct measure of a system's total thermal energy. The total thermal energy (sometimes called the total internal energy) of a system depends jointly on the temperature, the total number of atoms in the system, and the state of the material.; The term heat as used in everyday language refers both to thermal energy (the motion of atoms or molecules within a substance) and the transfer of that thermal energy from one object to another. In science, heat is used only for this second meaning; it refers to the energy transferred due to the temperature difference between two objects.; Temperature is not a measure of energy; the relationship between the temperature and the total energy of a system depends on the types, states, and amounts of matter present.",
