           "title":"Patterns",
           "info":"Students observe patterns in systems at different scales and cite patterns as empirical evidence for causality in supporting their explanations of phenomena. They recognize classifications or explanations used at one scale may not be useful or need revision using a different scale; thus requiring improved investigations and experiments. They use mathematical representations to identify certain patterns and analyze patterns of performance in order to reengineer and improve a designed system."
